.announcement-bar {
    padding: calc-em(9.5) 0;
    a {
        color: $secondary;
    }

    &__timer {

        @include media-breakpoint-down(md) {
            &__title {
                max-width: 45%;
            }

            &__countdown {
                max-width: 55%;
            }
        }

        .list-inline-item {
            @include media-breakpoint-down(lg) {
                font-size: calc-em(20);
            }
            &:not(:first-child) {
                &:before {
                    content: ':';
                    position: absolute;
                    left: -15px;
                }
            }

            span {
                display: block;
                font-size: calc-em(9);
                font-weight: normal;

                @include media-breakpoint-up(lg) {
                    font-size: calc-em(10);
                }
            }
        }
    }
}
